<template>
  <Header></Header>
  <span class="reg">{{ name }}</span>
  <button class="reg-button" @click="login" v-if="showSingIn">Войти</button>
  <button class="reg-button" @click="logout" v-if="!showSingIn">Выйти</button>
  <router-view v-if="!showSingIn"></router-view>
  <!--v-if="!showSingIn"-->
</template>

<script>
import Header from "@/components/Header.vue";
import Keycloak from "keycloak-js";

const keycloak = new Keycloak({
  url: "https://keys.urfu.ru/auth/",
  realm: "urfu-lk",
  clientId: "rtf-eduload",
  public_key:
      "",
  "token-service":
      "https://keys.urfu.ru/auth/realms/urfu-lk/protocol/openid-connect",
  "account-service": "https://keys.urfu.ru/auth/realms/urfu-lk/account",
  "tokens-not-before": 0,
});

export default {
  mounted() {
    this.updateKC();
  },
  components: {
    Header,
  },
  data() {
    return {
      showSingIn: true,
      name: "Not Auth",
      isAuth: false,
    };
  },
  methods: {
    async updateKC()
    {
      await keycloak.init({
        onLoad: "check-sso",
      });
      if (keycloak.authenticated) {
        this.name = keycloak.tokenParsed.user.person.title;
        this.showSingIn = false;
        let url = 'http://212.193.88.196:8080/adduser?login=' + keycloak.tokenParsed.user.person.email + '&isKeycloak=1&samAccountName=' + keycloak.tokenParsed.user.person.id;
        console.log(url);
        fetch(url).then((data) => {
          console.log(data);
        })}
    },
    async login() {
      if (!keycloak.authenticated) {
        await keycloak.login();
      } else {
        this.name = keycloak.tokenParsed.user.person.title;
        this.isAuth = true;
      }
    },
    async logout() {
      if (keycloak.authenticated) {
        await keycloak.logout();
      }
    },
  },
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.reg-button {
  padding: 10px;
  border-radius: 10px;
  font-size: 18px;
  margin-left: 10px;
  cursor: pointer;
}

.reg {
  display: inline-flex;
  margin-top: 20px;
  margin-left: 15px;
  font-size: 20px;
}
</style>
