<template>
  <button class="reg-button" @click="toMainPage" style="display: flex; margin-top: 20px">Назад</button>

  <div class="reg">Преподаватель: {{this.name}}</div>

  <div class="table" ><AGGrid
      :gridData="gridData"
      :columnDefs="columnDefs"
      :defaultColDef="defaultColDef"
      :getRowId="getRowId"
  ></AGGrid></div>
</template>

<script>
import AGGrid from "@/components/AGGrid.vue";

export default {
  mounted() {
    this.fetchJson();
  },
  components: {
    AGGrid,
  },
  data() {
    return {
      name,
      gridData: [],
      columnDefs: [
        { headerName: "№", valueGetter: 'node.id', width: 60, },
        { headerName: "Дисциплина", field: "disciplineName", width: 300, filter: true, floatingFilter: true,},
        { headerName: "Академическая группа", field: "groupsHistory", width: 160, filter: true, floatingFilter: true,},
        { headerName: "Семестр", field: "semester",  width: 160, filter: true, floatingFilter: true,},
        { headerName: "Вид нагрузки", field: "loadType", width: 195, filter: true, floatingFilter: true,},
        { headerName: "Формирующая кафедра", field: "formingDivisionuuid", width: 300, filter: true, floatingFilter: true,},
        { headerName: "Читающая кафедра", field: "readingDivisionuuid", width: 300, filter: true, floatingFilter: true,},
        { headerName: "Тип нагрузки", field: "compensationType", width: 140, filter: true, floatingFilter: true,},
        { headerName: "Планируемое кол-во часов", field: "plannedHours", width: 140,},
        { headerName: "Фактическое кол-во часов", field: "realHours", editable: true, width: 140,},
        { headerName: "Разница", field: "diff", width: 100},
        { headerName: "Почасовая оплата", field: "isHour", width: 120, cellRenderer: params => {
            if (params.value == 1) {
              return '<div><input type="checkbox" checked></div>';
            }
            else {
              return '<div><input type="checkbox"></div>';
            }
          },},
      ],
      defaultColDef: {
        sortable: true,
        resizable: true,
        spanHeaderHeight: true,
        wrapHeaderText: true,
        minWidth: 100,
        wrapText: true,
        autoHeight: true,
        autoHeaderHeight: true,
      },
    };
  },
  methods: {
    toMainPage() {
      window.location.href = '/';
    },
    async fetchJson() {
      let json;
      await fetch("http://212.193.88.196:8080/loaddatabytkey?tkey=" + this.$route.query.tkey)
          .then((resp) => resp.text())
          .then((razm) => (json = razm));
      let result = JSON.parse(json);
      this.gridData = result[0];
      this.name = result[1];
    },
  },
};
</script>

<style scoped>

.reg {
  display: flex;
  margin-top: 15px;
  margin-left: 15px;
  font-size: 20px;
}
.table {
  margin: 20px;
  display: flex;
  height: 850px;
  width: 2180px;
}
</style>