<template>
  <button @click="infoClick()"><img src="../img/info.png" alt=""></button>
</template>

<script>
export default {
  setup(props) {
    let name = props.params.value;
    function infoClick()
    {
      window.location.href = '/filtered?tkey=' + this.name.toString();
    }
    return {
      name, infoClick
    };
  }
}
</script>

<style scoped>
img {
  width: 28px;
}
button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
</style>